import React, {useEffect, useState} from 'react';
import './app.scss';

import Header from './components/Header'
import Movies from './components/Movies';

const F_API = 'https://api.themoviedb.org/3/discover/movie?api_key=16c26f3747cebe830cb8fec52ab18dea&language=ru&sort_by=popularity.desc&include_adult=false&include_video=false&page=1';
const SEARCH_API = 'https://api.themoviedb.org/3/search/movie?api_key=16c26f3747cebe830cb8fec52ab18dea&language=ru&query=';

const App = () => {
  const [ movie, setMovies ] = useState([]); 

  useEffect( () => {
    getMovies(F_API);
  }, [] );

  const getMovies = (apiUrl) => {
    fetch(apiUrl)
    .then( res => res.json() )
    .then( data => {
      setMovies(data.results);
    });
  }

  const handleOnSubmit = (searchValue) => {
    const searchUrl = searchValue ? SEARCH_API + searchValue : F_API;
    
    fetch(searchUrl)
      .then( res => res.json() )
      .then( data => {
        setMovies(data.results);
      });
  };

  return(
    <>
      <Header handleOnSubmit={handleOnSubmit} />
      <Movies movies={movie} />
    </>
  );
}

export default App;
