import React from 'react';
import './movie.scss';

const IMG_API = 'https://image.tmdb.org/t/p/w500';

const setVoteModif = (vote) => {
  switch (true) {
    case vote >= 8 :
        return 'movie__vote-green';
    break;
    case vote >= 6 :
        return 'movie__vote-orange';
    break;
    case vote < 6 :
      return 'movie__vote-red';
    break;
  }
}

const Movie = ({ title, poster_path, overview, vote_average }) => {
  return (
    <div className="movie">
      <div className="movie__header">
        <img className="movie__poster" src={IMG_API + poster_path} alt={title} />
        <div className="movie__info">
          <h3 >{title}</h3>
          <span className={`movie__vote ${setVoteModif(vote_average)}`}>{vote_average}</span>
        </div>
      </div>
      <div className="movie__over">
        <h2>Описание: </h2>
        <p>{ overview }</p>
      </div>
    </div>
);
}

export default Movie;